<template>
    <div class="wrapper">
        <div class="banner-left banner">
            <p class="title">
                <span>关</span>于我们
            </p>
            <p class="desc">
                秀泉映月温泉假日酒店
            </p>
            <p class="profile">
                <i>国家珍惜氟泉（泉珍），锶泉</i>
                酒店园区内有温泉，温泉日流量3000多吨。温泉水温常年保持高达49.8℃，泉水内富含 氡、氟、锶、锌、锂等40多种矿物质，多项指标达到了有医疗价值热矿水标准， 命名为含偏硅酸、偏硼酸、镭、氟、锶医疗热矿水 ，对人体具有显著的养生、医疗 、美容、保健等功效。
            </p>
        </div>
        <div class="banner-right banner">
            <Card class="card-wrapper" :data='item' v-for="(item, index) in datas" :key="index"/>
        </div>
    </div>
</template>
<script>
import Card from './card'
export default {
    props: {
        datas: {
            type: Array,
            default: () => []
        }
    },
    components: {
        Card
    }
}
</script>
<style lang="less" scoped>
@text-color: #f3cb6c;
.wrapper{
    width: 1170px;
    margin: 0 auto;
    text-align: left;
    .banner{
        width: 48%;
        height: 360px;
        display: inline-block;
    }
    .banner-left{
        .title{
            line-height: 80px;
            font-size: 2.5em;
            letter-spacing: 8px;
            span{
                font-size: 1.2em;
                color: @text-color;
            }
        }
        .desc{
            line-height: 40px;
            font-size: 2em;
            letter-spacing: 10px;
            margin: .8em 0 1.5rem 0;
        }
        .profile{
            line-height: 2em;
            color: #999;
            i{
                display: block;
                margin: .8em 0 1rem 0;
                letter-spacing: 10px;
                color: @text-color;
            }
        }
    }
    .banner-right{
        float: right;
        text-align: right;
        .card-wrapper{
            width: 48%;
            height: 180px;
            display: inline-block;
        }
    }
}
@media screen and(max-width: 1300px) {
    .wrapper{
        width: 1100px;
        .banner{
            width: 80%;
            height: auto;
            margin: 0 auto;
            display: block;
            text-align: center;
            &:nth-of-type(1){
                padding-bottom: 20px;
            }
        }
        .banner-right{
            float: none;
            .card-wrapper{
                height: 250px;
            }
        }
    }
}
@media screen and(max-width: 1100px) {
    .wrapper{
        width: 100%;
    }
}
@media screen and(max-width: 400px) {
    .wrapper{
        .banner-right{
            width: 95%;
            .card-wrapper{
                width: 100%;
                height: 220px;
            }
        }
    }
}
</style>





