<template>
    <div class="wrapper" ref="wrapper">
        <div class="item-wrapper">
            <Card class="card-wrapper" :data='item' v-for="(item, index) in datas" :key="index"/>
        </div>
    </div>    
</template>
<script>
import Card from './card'
import BScroll from 'better-scroll'
export default {
    props: {
        datas: {
            type: Array,
            default: () => []
        }
    },
    mounted() {
        this.initScroll()
    },
    methods: {
        initScroll () {
            this.scroll = new BScroll(this.$refs.wrapper, {
                click: true,
                scrollX: true
            })
        }
    },
    components: {
        Card
    }
}
</script>
<style lang="less" scoped>
.wrapper{
    width: 100%;
    .item-wrapper{
        display: flex;
        *{
            box-sizing: border-box;
        }
        width: 1170px;
        margin: 0 auto;
        position: relative;
        z-index: 5;
        .card-wrapper{
            width: 24%;
            height: 300px;
            display: inline-block;
            border: 1px solid transparent;
        }
    }
}
@media screen and(max-width: 1100px){
    .wrapper{
        .item-wrapper{
            padding: 0 50px;
        }
    }
}

</style>




