<template>
  <div id="app">
    <Header />
    <keep-alive>
      <router-view/>
    </keep-alive>
    <div class="to-top" v-show='!isTop' @click="backToTop">
        <span class="iconfont icon-shang"></span>
    </div>
  </div>
</template>

<script>
import Header from '_c/header'
import { iScrollTo } from 'common/js/tools'
export default {
  name: 'App',
  data() {
    return {
      isTop: true
    }
  },
  mounted() {
    window.addEventListener('scroll', e => {
      this.handleScrol(window.pageYOffset)
    })
  },
  components: {
    Header
  },
  methods: {
    handleScrol (x) {
      if(x <= 20) {
        this.isTop = true
      } else {
        this.isTop = false
      }
    },
    backToTop () {
        iScrollTo(0, window.scrollY)
    }
  },
}
</script>

<style lang='less'>
*{
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  .to-top{
      position: fixed;
      right: 60px;bottom: 70px;
      height: 30px;
      width: 30px;
      z-index: 99;
      background: rgba(0, 0, 0, 0.5);
      border-radius: 3px;
      cursor: pointer;
      .iconfont{
          line-height: 30px;
          color: white;
      }
  }
}
@media screen and(max-width: 400px) {
  #app {
    .to-top{
      right: 30px;bottom: 35px;
    }
  }
}
</style>



