export default {
    state: {
        articleList: [],
        articleIndex: 0,
    },
    getters: {
        currentArticle: state => state.articleList[state.articleIndex],
        currentArticleIndex: state => state.articleIndex,
        articleList: state => state.articleList
    },
    mutations: {
        set_articleList (state, list) {
            state.articleList = list
        },
        set_articleIndex (state, index) {
            state.articleIndex = index
        }
    }
}


