<template>
    <div class="wrapper">
        <p class="title" @click="goRoom">
            <span>房</span>
            <span>间</span>
            <span>展</span>
            <span>示</span>
        </p>
        <div class="img-wrapper">
            <Card class="card-wrapper" :data='item' v-for="(item, index) in datas" :key="index"
             @on-click="checkItem(item)" />
        </div>
    </div>    
</template>
<script>
import Card from './card'
export default {
    props: {
        datas: {
            type: Array,
            default: () => []
        }
    },
    methods: {
        goRoom () {
            this.$router.push({
                name: `imagelist`, 
                query: {
                    cateid: 6,
                    cateindex: 0
            }})
        },
        checkItem (item) {
            this.$router.push({
                path: '/imgreader',
                query: {
                id: item.id
            }})
        }
    },
    components: {
        Card
    }
}
</script>
<style lang="less" scoped>
.wrapper{
    .title{
        height: 80px;
        line-height: 80px;
        font-size: 3em;
        padding: 40px 0;
        cursor: pointer;
        span{
            display: inline-block;
            padding: 0 10px;
            border: 1px solid #e0c073;
            color: #e0c073;
        }
    }
    .img-wrapper{
        width: 1170px;
        margin: 0 auto;
        box-sizing: border-box;
        padding-left: 15px;
        &::after{
            content: '';
            display: block;
            clear: both;
        }
    }
}
@media screen and(max-width: 1170px) {
    .wrapper{
        .img-wrapper{
            width: 950px;
        }
    }
}
@media screen and(max-width: 950px) {
    .wrapper{
        .img-wrapper{
            width: 660px;
        }
    }
}
@media screen and(max-width: 660px) {
    .wrapper{
        .img-wrapper{
            width: 330px;
        }
    }
}
</style>



