import { postData, getData } from './axios.conf'
import { navigatorMap } from 'common/js/navigators'

// 获取导航数据
export function getNavigators () {
    return navigatorMap
}

// 获取文章  单篇文章 对象
export async function getArticle(cateid, cateindex, id) {
    let result = {}
    if (id) {
        let res = await getData('article', { id })
        if (res.code === 0 && res.data.length) {
            result = res.data[0]
        }
    } else if(cateid) {
        cateindex = cateindex ? cateindex : 0
        let res = await getData('article', { cateid, cateindex })
        if (res.code === 0 && res.data.length) {
            result = res.data[0]
        }
    }
    return result
}

// 文章 列表 数组
export async function getArticleList (cateid, cateindex, page, size) {
    let result = []
    cateid = cateid ? cateid : 0
    cateindex = cateindex ? cateindex : 0
    let res = await getData('articlelist', { cateid, cateindex, page, size })
    if (res.code === 0 && res.data.length) {
        result = res.data
    }
    return result
}
// 图文 单个 对象
export async function getImage (cateid, cateindex, id) {
    let result = {}
    if (id) {
        let res = await getData('image', { id })
        if (res.code === 0 && res.data.length) {
            result = res.data[0]
        }
    } else if(cateid) {
        cateindex = cateindex ? cateindex : 0
        let res = await getData('image', { cateid, cateindex })
        if (res.code === 0 && res.data.length) {
            result = res.data[0]
        }
    }
    let imgUrls = result.imgUrls ? JSON.parse(result.imgUrls) : []
    return Object.assign(result, {
        imgUrls: imgUrls
    })
}

// 图片 列表 数组
export async function getImageList (cateid, cateindex, page, size ) {
    let result = []
    cateid = cateid ? cateid : 0
    cateindex = cateindex ? cateindex : 0
    let res = await getData('imagelist', { cateid, cateindex, page, size })
    if (res.code === 0 && res.data.length) {
        result = res.data
    }
    return result.map(item => {
        let imgUrls = item.imgUrls ? JSON.parse(item.imgUrls) : []
        return Object.assign(item, {
            imgUrls: imgUrls,
            imgUrl: imgUrls[0]
        })
    })
}
// 获取首页展示信息
export async function getDispalyImage (imgcate, size) {
    let result = []
    let res = await getData('displayimage', { imgcate, size })
    if (res.code === 0 && res.data.length) {
        result = res.data
    }
    return result.map(item => {
        let imgUrls = item.imgUrls ? JSON.parse(item.imgUrls) : []
        return Object.assign(item, {
            imgUrls: imgUrls,
            imgUrl: imgUrls[0]
        })
    })
}


