import { mapGetters, mapActions, mapMutations } from 'vuex'

export const resizeMixin = {
    mounted() {
        window.addEventListener('resize', () => {
            this.resets({height: window.innerHeight, width: window.innerWidth})
            if (window.innerWidth < 400) {
                this.setHeight = 250
            } else {
                this.setHeight = undefined
            }
        })
        this.resets({height: window.innerHeight, width: window.innerWidth})
    },
    methods: {
        getFullEl() {
            throw new Error('you must implement getFullEl methods, return an element array')
        },
        changeSize () {
            let els = this.getFullEl()
            for (let i = 0; i < els.length; i++) {
                let el = els[i]
                el.style.height = this.setHeight ? this.setHeight + 'px' : this.height + 'px'
                if (this.width > 1200) {
                    el.style.width = this.width + 'px'
                } else if (window.innerWidth < 400) {
                    el.style.width = window.innerWidth + 'px'
                } else {
                    el.style.width = 1200 + 'px'
                }
            }
        },
        ...mapActions(['resets'])
    },
    computed: {
        ...mapGetters(['height', 'width'])
    },
    watch: {
        width() {
            this.$nextTick(() => {
                this.changeSize()
            })
        }
    },
}

export const articleMixin = {
    methods: {
        ...mapMutations(['set_articleList', 'set_articleIndex']),
        handleArticleIndexChange () {
            // console.log('index')
        },
        handleArticleListChange () {
            console.log('list')
        },
        handleCurrentArticle () {
            console.log('article')
        }
    },
    computed: {
        ...mapGetters(['currentArticle', 'currentArticleIndex', 'articleList'])
    },
    watch: {
        articleList () {
            this.handleArticleListChange()
        },
        currentArticleIndex () {
            this.handleArticleIndexChange()
        },
        currentArticle () {
            this.handleCurrentArticle()
        }
    }
}


