<template>
    <div class="card">
        <div class="img item" :style="{'background-image': `url('${data.imgUrl}')`}">

        </div>
        <div class="text item">
            <p class="title">
                {{data.title}}
            </p>
            <p class="desc">
                {{data.desc}}
            </p>
            <p class="btn">
                <Ibutton :big='true' content='查看更多' @btnclick="handleClick"/>
            </p>
        </div>
    </div>
</template>
<script>
import Ibutton from '_c/ibutton'
export default {
    props: {
        data: {
            type: Object,
            default: () => {
                return {
                    title: '最美2温泉',
                    desc: '最美温泉描述',
                    imgUrl: 'http://demo.cssmoban.com/cssthemes5/twts_72_diner/img/img_2.jpg'
                }
            }
        }
    },
    methods: {
        handleClick () {
            this.$emit('on-click')
        }
    },
    components: {
        Ibutton
    }
}
</script>
<style lang="less" scoped>
.card{
    height: 440px;
    width: 100%;
    .item{
        height: 100%;
        width: 50%;
        float: left;
    }
    .img{
        background-position: center;
        background-size: cover;
    }
    .text{
        box-sizing: border-box;
        padding: 50px;
        background: #000;
        position: relative;
        .title{
            font-size: 32px;
            color: white;
            padding-bottom: 24px;
        }
        .desc{
            line-height: 29px;
            text-align: left;
            text-indent: 2em;
            color: #999;
        }
        .btn{
            width: 100%;
            position: absolute;
            bottom: 40px;
            text-align: left;
        }
    }
}
@media screen and(max-width: 950px) {
    .card{
        .item{
            height: 50%;
            width: 100%;
        }
        .text{
            padding: 20px;
            .btn{
                width: 88%;
                box-sizing: border-box;
                position: absolute;
                bottom: 15px;
                text-align: right;
            }
        }
    }
}
@media screen and(max-width: 420px) {
    .card{
        .text{
            .btn{
                text-align: center;
            }
        }
    }
}
</style>



