<template>
  <div class="header" :class="{'header-active': true, 'header-show': headerShow}">
    <div class="head-wrapper">
      <div class="top_box">
        <span href="/" class="logo">
          <img :src="require('common/img/logo.png')" alt="">
        </span>
        <div class="nav">
          <ul v-if="naviData.length">
            <li v-for="(item, index) in naviData" :key="index" @mouseover="hoverNav(index)">
              <a @click="handleRouterTo(item)">
                {{item.parent}}
                <p>{{item.enTitle}}</p>
              </a>
            </li>
          </ul>
        </div>
      </div>
      <div class="toggle" @click="toggleHeaderShow">
        <span class="iconfont" :class="headerShow ? 'icon-cha' : 'icon-hanbao'"></span>
      </div>

      <div class="second-nav" @mouseleave="hoveOut">
        <div class="sec-navs">
          <div class="subnav">
            <div
              :class="['subnav_item', {'subnav_item-active': index == currentIndex - 1}]"
              v-for="(item, index) in naviData.slice(1)"
              :key="index"
            >
              <ul v-if="item.children">
                <li v-for="(child, son_index) in item.children" :key="son_index">
                  <a @click="handleRouterTo(child)">{{child.title}}</a>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div class="sub-info">
          <div class="sub_in">
            <div class="sub_qr">
              <ul>
                <li>
                  <span>
                    <img :src="qr1">
                  </span>
                  <p>秀全公众号</p>
                </li>
                <li v-if="false">
                  <span>
                    <img :src="qr1">
                  </span>
                  <p>联系客服</p>
                </li>
              </ul>
            </div>
            <div class="sub_tel">
              <span>统一订房热线</span>
              <p>023-66450999</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { selectArray } from "common/js/navigators";
import { getQr } from '@/api/linkimg'
export default {
  data() {
    return {
      qr1: require("common/img/qr_code01.jpg"),
      currentIndex: -1,
      headerShow: false
    };
  },
  mounted() {
    this.qr1 = getQr(1)
  },
  methods: {
    toggleHeaderShow () {
      this.headerShow = !this.headerShow
    },
    hoverNav(index) {
      this.currentIndex = index;
    },
    hoveOut() {
      this.currentIndex = -1;
    },
    handleRouterTo(item) {
      // 点击父类，进入第一个索引
      if (item.children) {
        item = item.children[0];
      }
      if (item.parent == "首页") {
        this.$router.push({
          name: "homepage"
        });
        return;
      }
      this.$router.push({
        name: item.displaytype,
        query: {
          cateid: item.cateid,
          cateindex: item.cateindex
        }
      });
    }
  },
  computed: {
    naviData() {
      return selectArray;
    }
  }
};
</script>
<style lang="less" scoped>
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  box-shadow: 0 0 3px #aaa;
  min-width: 1200px;
  transition: all .5s;
  .head-wrapper {
    background: white;
    .toggle {
      display: none;
      position: absolute;
      right: 10px;bottom: 0;
      height: 30px;width: 30px;
      transform: translateY(100%);
      cursor: pointer;
      .iconfont{
        color: #e0c073;
        font-size: 30px;
      }
    }
    .top_box {
      width: 91.6%;
      margin: 0 auto;
      overflow: hidden;
      position: relative;
      .logo {
        position: absolute;
        top: 18px;
        left: 0;
        img {
          height: 43px;
        }
      }
      .nav {
        float: right;
        width: 74%;
        ul {
          overflow: hidden;
          li {
            cursor: pointer;
            float: left;
            width: 12.5%;
            a {
              display: block;
              text-align: center;
              font-size: 0;
              padding: 20px 10px;
              font-size: 17px;
              // color: #888;
              color: #e0c073;
              p {
                // color: #e0c073;
                color: #777;
                font-size: 12px;
                text-transform: uppercase;
                margin-top: 2px;
              }
              &:hover {
                background: #e0c073;
                color: white;
                p {
                  color: #7e6321;
                }
              }
            }
          }
        }
      }
    }
    .second-nav {
      opacity: 0;
      position: absolute;
      width: 100%;
      top: 100%;
      visibility: hidden;
      background: linear-gradient(
        left top,
        rgb(224, 192, 115),
        rgba(224, 192, 115, 0) 40%,
        transparent
      );
      background: -o-linear-gradient(
        left top,
        rgb(224, 192, 115),
        rgba(224, 192, 115, 0) 40%,
        transparent
      );
      background: -moz-linear-gradient(
        left top,
        rgb(224, 192, 115),
        rgba(224, 192, 115, 0) 40%,
        transparent
      );
      background: -webkit-gradient(
        linear,
        left top,
        right bottom,
        from(#e0c073),
        color-stop(40%, rgba(224, 192, 115, 0)),
        to(transparent)
      );
      transition: all 0.4s;
      z-index: 99;
      .sec-navs {
        width: 91.6%;
        margin: 0 auto;
        overflow: hidden;
        .subnav {
          width: 74%;
          float: right;
          height: 220px;
          .subnav_item {
            float: left;
            visibility: hidden;
            width: 12.5%;
            min-height: 1px;
            &:nth-of-type(1) {
              margin-left: 12.5%;
            }
            ul {
              overflow: hidden;
              li {
                cursor: pointer;
                margin: 5px 0;
                a {
                  display: block;
                  font-size: 14px;
                  color: #000;
                  text-align: center;
                  padding: 14px 0;
                  overflow: hidden;
                  text-overflow: ellipsis;
                  white-space: nowrap;
                  transition: background .1s;
                  &:hover {
                    background: #f1f1f1;
                    color: #e0c073;
                  }
                }
              }
            }
          }
          .subnav_item-active {
            visibility: visible;
            background: #e0c073;
            border-top: 1px solid #888;
          }
        }
      }
      .sub-info {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        .sub_in {
          display: flex;
          flex-direction: column;
          align-items: center;
          overflow: hidden;
          padding: 15px 0 15px 80px;
          .sub_tel {
            padding-left: 65px;
            background: url(./tel.png) no-repeat left center;
            background-size: contain;
            float: left;
            margin-top: 33px;
            span {
              color: #000;
              font-size: 14px;
            }
            p {
              color: #000;
              font-family: Arial;
              font-size: 26px;
              margin-top: 3px;
              font-weight: bold;
            }
          }
          .sub_qr {
            float: right;
            margin-right: 15px;
            ul {
              overflow: hidden;
              margin: 0 -25px;
              li {
                float: left;
                margin: 0 25px;
                span {
                  display: block;
                  width: 80px;
                  height: 80px;
                  overflow: hidden;
                  img {
                    width: 100%;
                  }
                }
                p {
                  color: #000;
                  font-size: 12px;
                  text-align: center;
                  margin-top: 5px;
                }
              }
            }
          }
        }
      }
    }
    &:hover {
      .second-nav {
        visibility: visible;
        transition: all 1s;
        opacity: 1;
      }
    }
  }
}
.header-active{
    box-shadow: 0 2px 10px #aaa;
}
@media screen and(max-width: 1100px) {
  .header{
    width: 100%;
    min-width: auto;
    .head-wrapper{
      .top_box{
        margin-left: 10px;
        .logo{
          display: none;
        }
        .nav{
          width: 100%;
        }
      }
      .second-nav {
        .sec-navs{
          margin-left: 10px;
          .subnav{
            width: 100%;
          }
        }
      }
      .sub-info{
        display: none;
      }
    }
  }
}
@media screen and(max-width: 400px) {
  .header{
    transform: translateY(-100%);
    .head-wrapper{
      .top_box{
        .logo{
          
        }
        .nav{
          
        }
      }
      .toggle{
        display: block;
      }
      .second-nav {
        display: none;
      }
    }
  }
  .header-show {
    transform: translateY(0);
  }
}
</style>




