<template>
    <div class="slider">
        <ul class="imgs" :style="{width: images.length * 500 + '%'}" ref="imgsWrapper">
            <li class="img-item" v-for="(img, index) in images" :key="index" ref="imgItems">
                <img :src="img" alt="">
            </li>
        </ul>
        <div class="ctrl">
            <div class="ctrl-item" :class="{'ctrl-item-active': currentIndex == index}"
             v-for="(img, index) in images" :key="index" @click="selectImg(index)"></div>
        </div>
        <div class="icon houtui" @click="selectImg('pre')">
            <span class="iconfont icon-houtui"></span>
        </div>
        <div class="icon qianjin" @click="selectImg('next')">
            <span class="iconfont icon-qianjin"></span>
        </div>
    </div>    
</template>

<script>
import {resizeMixin} from 'common/js/mixin'
export default {
    mixins: [resizeMixin],
    props: {
        images: {
            type: Array,
            default: () => []
        }
    },
    data () {
        return {
            currentIndex: 0,
            setHeight: 700
        }
    },
    mounted() {
        // 重置宽高
        this.changeSize()
        // 自动播放
        clearTimeout(this.timer)
        this.timer = setTimeout(() => {
            this.play()
        }, 4000)
    },
    methods: {
        play() {
            this.currentIndex = ++this.currentIndex == this.images.length ? 0 : this.currentIndex
            this.transImgs(this.currentIndex)
        },
        selectImg (index) {
            if (typeof index == 'string') {
                if (index == 'next') {
                    this.currentIndex = ++this.currentIndex == this.images.length ? 0 : this.currentIndex
                }
                if (index == 'pre') {
                    this.currentIndex = --this.currentIndex < 0 ? this.images.length - 1 : this.currentIndex
                }
                this.transImgs(this.currentIndex)
                return
            }
            this.currentIndex = index
            this.transImgs(index)
        },
        transImgs(index) {
            let width = window.innerWidth > 1200 
                        ? window.innerWidth 
                        : 1200
            if (window.innerWidth < 400) {
                width = window.innerWidth
            }
            if (this.$refs.imgsWrapper)
                this.$refs.imgsWrapper.style.transform = `translateX(-${width * index}px)`
        },
        getFullEl() {
            return this.$refs.imgItems ? this.$refs.imgItems : []
        }
    },
    watch: {
        currentIndex() {
            clearTimeout(this.timer)
            this.timer = setTimeout(() => {
                this.play()
            }, 4000)
        }
    },
}
</script>
<style lang="less" scoped>
.slider{
    box-sizing: border-box;
    position: relative;
    height: 100%;
    overflow: hidden;
    .imgs{
        height: 100%;    
        transition: all .5s;
        .img-item{
            height: 100%;    
            float: left;
            img{
                height: 100%;
                width: 100%;
            }
        }
    }
    .ctrl{
        width: 100%;
        position: absolute;
        bottom: 40px;
        z-index: 10;
        display: flex;
        justify-content: center;
        .ctrl-item{
            height: 30px;
            width: 30px;
            background-color: #ccc;
            border-radius: 50%;
            transform: scale(0.5);
            transition: background-color .5s;
            cursor: pointer;
        }
        .ctrl-item-active{
            background-color: #e0c073 
        }
    }
    .icon{
        position: absolute;
        top: 50%;
        cursor: pointer;
        span{
            font-size: 50px;
            padding: 15px 3px;
            background: radial-gradient( rgba(255,255,255, 0.2), rgba(255,255,255, 0));
            color: rgba(0, 0, 0, 0.8);
        }
        &:hover{
            span{
                background: radial-gradient( rgba(255,255,255, 0.6), rgba(255,255,255, 0));
            }
        }
    }
    .qianjin{
        right: 40px;
    }
    .houtui{
        left: 40px;
    }
}
@media screen and(max-width: 400px){
    .slider{
        .ctrl{
            bottom: 10px;
        }
        .icon{
            top: 45%;
            span{
                font-size: 30px;
            }
        }
        .qianjin{
            right: 20px;
        }
        .houtui{
            left: 20px;
        }
    }
}
</style>




