// 获取两级菜单的数组 article articlelist 参数
export const selectArray = (() => {
    let articleType = []
    let temp = {}
    navigatorMap.forEach(item => {
        if (item.cateid !== 0) {
            if (!temp[item.cateid]) {
                articleType.push({
                    parent: item.parent,
                    cateid: item.cateid,
                    enTitle: item.enTitle,
                    children: [item]
                })
                temp[item.cateid] = true
            } else {
                articleType[articleType.length - 1].children.push(item)
            }
        }
    })
    articleType.unshift({
        parent: '首页',
        enTitle: 'Home'
    })
    return articleType
})()

export const getFilterType = (typesArray) => {
    let resArray = []
    let temp = {}
    typesArray.forEach(item => {
        if (!temp[item.value]) {
            resArray.push(item)
            temp[item.value] = true
        }
    })
    return resArray
}


