<template>
    <div class="card">
        <div class="img" :style="{'background-image': `url(${data.imgUrl})`}"></div>
        <div class="text">
            <span class="iconfont" :class="data.iconfont"></span>
            <p class="title">{{data.title}}</p>
            <p class="desc">{{data.desc}}</p>
        </div>
    </div>
</template>
<script>
export default {
    props: {
        data: {
            type: Object,
            default: () => {
                return {
                    title: '标题',
                    desc: '描述',
                    imgUrl: require('@/common/img/cont2.png')
                }
            } 
        }
    }
}
</script>
<style lang="less" scoped>
.card{
    font-size: 0;
    box-sizing: border-box;
    background: #fff;
    padding: 2px;
    position: relative;
    .img{
        height: 100%;
        width: 100%;
        background-size: 100% 100%;
        background-position: center;
        transition: all .5s;
    }
    .text{
        width: 100%;
        position: absolute;
        bottom: 0;top: 0;
        box-sizing: border-box;
        padding: 20px 15px;
        text-align: center;
        opacity: 0;
        background: rgba(0, 0, 0, 0.5);
        transition: all .5s;
        .iconfont{
            font-size: 30px;
            color: #f3cb6c;
        }
        .title{
            font-size: 17px;
            margin: 5px 0;
            color: #f3cb6c;
        }
        .desc{
            font-size: 14px;
            color: #fff;
            line-height: 1.8em;
        }
    }
    &:hover{
        .img{
            background-size: 120% 120%;
        }
        .text{
            opacity: 1;
        }
    }
}
</style>



