<template>
    <div class="card" @click.stop.prevent="handleClick">
        <div class="img">
            <img :src="data.imgUrl">
        </div>
        <div class="text-wrapper">
            <div class="text">
                <p class="title">{{data.title}}</p>
                <p class="desc">{{data.desc}}</p>
                <p class="price">挂牌价：￥{{data.price}}</p>
            </div>
        </div>
    </div>    
</template>
<script>
export default {
    props: {
        data: {
            type: Object,
            default:() => {
                return {
                    title: '标题',
                    desc: '描述',
                    imgUrl: require('@/common/img/cont2.png'),
                    price: 111
                }
            }
        }
    },
    methods: {
        handleClick () {
            this.$emit('on-click')
        }
    },
}
</script>
<style lang="less" scoped>
.card{
    width: 350px; 
    height: 230px;
    float: left;
    margin: 0 15px 30px 15px;
    position: relative;
    cursor: pointer;
    .img{
        height: 100%;
        width: 100%;
        img{
            height: 100%;
            width: 100%;
        }
    }
    .text-wrapper{
        position: absolute;
        top: 0;
        bottom: 0;
        width: 100%;
        padding: 15px;
        box-sizing: border-box;
        transition: all .4s ease;
        transform: scale(0) rotateX(-360deg);
        .text{
            height: 100%;width: 100%;
            box-sizing: border-box;
            background-color: rgba(0, 0, 0, 0.72);
            padding: 35px 56px;
            position: relative;
            .title{
                color: #e0c073;
                font-size: 1.5em;
                margin-bottom: 16px;
            }
            .desc{
                color: white;
                line-height: 1.8em;
                font-size: 15px;
            }
            .price{
                height: 35px;
                line-height: 35px;
                position: absolute;
                bottom: 0;right: 20px;
                color: white;
                font-size: 14px;
            }
        }
    }
    &:hover{
        .text-wrapper{
            transform: scale(1) rotateX(0);
        }
    }
}
@media screen and(max-width: 1170px) {
    .card {
        width: 280px; 
        height: 194px;
    }
}
</style>



