<template>
    <div class="wrapper">
        <p class="title">
            <span>联</span>
            <span>系</span>
            <span>我</span>
            <span>们</span>
        </p>
        <div class="contact">
            <div class="img">
                <img :src="require('common/img/cont2.png')">
                <div class="text">
                    <p class="fax">传真：023-66450999</p>
                    <p class="tel">联系电话：023-66450999</p>
                    <p class="addr">酒店地址：重庆市巴南区东泉镇东泉正街102号</p>
                </div>
            </div>
            <div class="news">
                <div class="art-title">
                    新闻资讯
                    <span class="more" @click="viewMore">更多 + </span>
                </div>
                <div class="art-list" ref="artlist">
                    <ul>
                        <li class="art-item" v-for="(item, index) in list" :key="index" @click="readArt(item)">
                            <span class="iconfont icon-taoxin"></span>
                            <span class="cont">
                            {{item.title}}
                            </span>
                            <span class="time">{{item.create_time | date}}</span>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { getArticleList } from '@/api'
import { articleMixin } from 'common/js/mixin'
import BScroll from 'better-scroll'
export default {
    mixins: [articleMixin],
    data () {
        return {
            list: []
        }
    },
    mounted() {
        this.getList()
        this.initScroll()
    },
    methods: {
        initScroll () {
            if (!this.scroll) {
                this.scroll = new BScroll(this.$refs.artlist, {
                    click: true
                })
            } else {
                this.scroll.refresh()
            }
        },
        async getList () {
            let res = await getArticleList(2, 0)
            this.list = res
            // for (let i = 0; i < 20; i++) {
            //     this.list.push({
            //         title: '标题',
            //         create_time: new Date().getTime()
            //     })
            // }
        },
        viewMore () {
            this.$router.push({
                name: `articlelist`, 
                query: {
                    cateid: 2,
                    cateindex: 0
            }})
        },
        readArt (article) {
            let index = this.list.findIndex(item => item.id == article.id)
            this.set_articleList(this.list)
            this.set_articleIndex(index)
            this.$router.push({ path: '/artreader' })
        }
    },
    filters: {
        date (t) {
            t = t ? new Date(Number(t)) : new Date(1545977032592)
            let reg = /\//g
            return t.toLocaleDateString().replace(reg, '-')
        }
    }
}
</script>
<style lang="less" scoped>
.wrapper{
    width: 1270px;
    margin: 0 auto;
    text-align: left;
    .title{
        height: 80px;
        line-height: 80px;
        font-size: 3em;
        padding: 40px 0 80px 0;
        text-align: center;
        span{
            display: inline-block;
            padding: 0 10px;
            border: 1px solid #e0c073;
            color: #e0c073;
        }
    }
    .contact{
        padding-bottom: 80px;
        .img{
            width: 550px;
            height: 340px;
            position: relative;
            display: inline-block;
            text-align: left;
            overflow: hidden;
            img{
                height: 100%;
                width: 100%;
            }
            .text{
                width: 100%;
                position: absolute;
                bottom: 0;
                height: 0;
                background: rgba(0, 0, 0, .5);
                padding-left: 50px;
                box-sizing: border-box;
                transition: all .5s;
                p{
                    color: white;
                    font-size: 14px;
                    line-height: 30px;
                }
            }
            &:hover{
                .text{
                    height: 100px;
                }
            }
        }
        .news{
            width: 550px;
            height: 340px;
            float: right;
            overflow: hidden;
            .art-title{
                font-size: 30px;
                color: #e0c073;
                text-align: left;
                padding: 0 10px 10px 10px;
                border-bottom: 1px solid #e0c073;
                .more{
                    color: #222;
                }
                span{
                    float: right;
                    font-size: 14px;
                    line-height: 40px;
                    cursor: pointer;
                }
            }
            .art-list{
                height: 289px;
                overflow: hidden;
                .art-item{
                    padding: 7px 10px;
                    text-align: left;
                    vertical-align: top;
                    line-height: 21px;
                    box-sizing: border-box;
                    color: #222;
                    border-bottom: 1px solid #ddd;
                    &:first-of-type{
                        padding-top: 20px;
                    }
                    .iconfont{
                        float: left;
                        color: rgb(245, 156, 171);
                    }
                    .cont{
                        cursor: pointer;
                        padding: 0 15px;
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        display: inline-block;
                        width: 70%;
                        &:hover{
                            color: #e0c073
                        }
                    }
                    .time{
                        float: right;
                    }
                }
            }
        }
    }
}
@media screen and(max-width: 1270px){
    .wrapper{
        width: 90%;
        .contact{
            .img{
                width: 440px;
                height: 272px
            }
            .news{
                width: 440px;
                height: 272px;
                .art-title{
                    font-size: 25px;
                }
            }
        }
    }
}
@media screen and(max-width: 1000px){
    .wrapper{
        width: 90%;
        .contact{
            .img{
                width: 330px;
                height: 204px
            }
            .news{
                width: 330px;
                height: 204px;
                .art-title{
                    font-size: 20px;
                }
                .art-list{
                    width: 100%;
                    .art-item{
                        .cont {
                            width: 50%
                        }
                    }
                }
            }
        }
    }
}
@media screen and(max-width: 760px){
    .wrapper{
        width: 90%;
        .contact{
            .img{
                margin: 0 auto;
                display: block;
            }
            .news{
                margin: 20px auto 0 auto;
                float: none;
                .art-title{
                    font-size: 20px;
                }
            }
        }
    }
}
</style>



