import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '_v/homepage'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'homepage',
      component: HomePage
    }, {
      path: '/viewpage',
      name: 'viewpage',
      component: () => import('_v/viewpage'),
      children: [{
        path: 'article',
        name: 'article',
        component: () => import('_v/viewpage/article')
      }, {
        path: 'articlelist',
        name: 'articlelist',
        component: () => import('_v/viewpage/articlelist')
      }, {
        path: 'image',
        name: 'image',
        component: () => import('_v/viewpage/image')
      }, {
        path: 'imagelist',
        name: 'imagelist',
        component: () => import('_v/viewpage/imagelist')
      }, {
        path: 'other',
        name: 'other',
        component: () => import('_v/viewpage/other')
      }]
    }, {
      path: '/artreader',
      name: 'artreader',
      component: () => import('_v/readartlist')
    }, {
      path: '/imgreader',
      name: 'imgreader',
      component: () => import('_v/readerimgs')
    }
  ]
})



