<template>
    <div class="card">
        <div class="img">
            <img :src="data.imgUrl">
        </div>
        <div class="title">
            {{data.title}}
        </div>
        <div class="desc">
             {{data.desc}}
        </div>
    </div>
</template>
<script>
export default {
    props: {
        data: {
            type: Object,
            default:() => {
                return {
                    title: '标题',
                    desc: '描述',
                    imgUrl: require('@/common/img/cont2.png')
                }
            } 
        }
    }
}
</script>
<style lang="less" scoped>
.card{
    height: 450px;
    width: 350px;
    padding: 10px;
    box-sizing: border-box;
    text-align: center;
    display: inline-block;
    margin: 0 20px;
    background: white;
    transform: translateY(-120px);
    transition: all .2s;
    .img{
        width: 100%;
        max-height: 250px;
        img{
            width: 100%;
            height: 100%;
        }
    }
    .title{
        height: 70px;
        line-height: 70px;
        font-size: 1.7em;
    }
    .desc{
        line-height: 27px;
        color: #999;
    }
    &:hover{
        box-shadow: 0 10px 30px #aaa;
        transform: translateY(-130px);
    }
}
@media screen and(max-width: 1186px) {
    .card{
        &:nth-of-type(1), &:nth-of-type(2){
            transform: translateY(-170px);
            &:hover{
                box-shadow: 0 10px 30px #aaa;
                transform: translateY(-180px);
            }
        }
    }
}
@media screen and(max-width: 796px) {
    .card{
        &:nth-of-type(1){
            transform: translateY(-220px);
            &:hover{
                box-shadow: 0 10px 30px #aaa;
                transform: translateY(-230px);
            }
        }
        &:nth-of-type(2){
            transform: translateY(-170px);
            &:hover{
                box-shadow: 0 10px 30px #aaa;
                transform: translateY(-180px);
            }
        }
    }
}
@media screen and(max-width: 400px) {
    .card{
        margin: 0;
        width: 100%;
    }
}
</style>



