<template>
    <div class="wrapper">
        <Card class="card-wrapper" :data='item' v-for="(item, index) in datas" :key="index"/>
    </div>
</template>
<script>
import Card from './card'
export default {
    props: {
        datas: {
            type: Array,
            default: () => []
        }
    },
    components: {
        Card
    }
}
</script>
<style lang="less" scoped>

</style>


