<template>
    <div class="ibutton" :class="{'big-btn': big}" @click.stop.prevent="handleClick">
        {{content}}
    </div>    
</template>
<script>
export default {
    props: {
        content: {
            type: String,
            default: 'button'
        },
        big: {
            type: Boolean,
            default: false
        }
    },
    methods: {
        handleClick () {
            this.$emit('btnclick')
        }
    },
}
</script>
<style lang="less" scoped>
@line-color: #e0c073;
.ibutton{
    padding: 10px 20px;
    position: relative;
    display: inline-block;
    box-shadow: 0 0 1px #ccc;
    cursor: pointer;
    color: #e0c073;
    box-sizing: border-box;
    .bor() {
        content: '';
        display: block;
        position: absolute;
        box-sizing: border-box;
        width: 0;height: 0;
    }
    &::before{
        .bor();
        left: 0;
        top: 0;
    }
    &::after{
        .bor();
        right: 0;
        bottom: 0;
    }
    &:hover{
        font-weight: 700;
        &::before{
            width: 100%;
            height: 100%;
            border-top: 1px solid @line-color;
            border-right: 1px solid @line-color;
            transition: width .4s, height .4s .4s;
        }
        &::after{
            width: 100%;
            height: 100%;
            border-left: 1px solid @line-color;
            border-bottom: 1px solid @line-color;
            transition: width .4s .8s, height .4s 1.2s;
        }
    }
}
.big-btn{
    padding: 10px 50px;
    letter-spacing: 2px;
    &:hover{
        transition: all 1s 1.2s;
        background-color: #e0c073;
        color: white;
    }
}
</style>




