<template>
    <div class="homepage">
        <div class="section1 main">
            <Slider :images='sliderImg'/>
        </div>
        <div class="section2 main">
            <Section2 :datas='section2Data'/>
        </div>
        <div class="section3 main">
            <Section3 :datas='section3Data.datas'/>
            <div class="mask" ref="mask" :style="{'background-image': `url('${section3Data.maskBg}')`}">
                <!-- translate3d -->
            </div>
        </div>
        <div class="section4 main" :style="{'background-image': `url('${section4Data.bg}')`}">
            <Section4 :datas='section4Data.datas'/>
        </div>
        <div class="section5 main">
            <Section5 :datas='section5Data'/>
        </div>
        <div class="section6 main" :style="{'background-image': `url('${section6Data.bg}')`}">
            <Section6 :datas='section6Data.datas'/>
        </div>
        <div class="section7 main" :style="{'background-image': `url('${require('common/img/gray_bg.jpg')}')`}">
            <Section7 />
        </div>
        <div class="footer-wrapper">
            <Footer />
        </div>
    </div>
</template>
<script>
import Slider from '_c/slider'
import Ibutton from '_c/ibutton'
import Section2 from './section2'
import Section3 from './section3'
import Section4 from './section4'
import Section5 from './section5'
import Section6 from './section6'
import Section7 from './section7'
import Footer from '_c/footer'
import * as linkimg from '@/api/linkimg'
export default {
    data() {
        return {
            contentPage: 1,
            contentMax: 6,
            scrollInterval: 1000,
            lastScrollTime: 0,
            isActive: true,
            sliderImg: [],
            section2Data: [],
            section3Data: {},
            section4Data: {},
            section5Data: [],
            section6Data: {}
        }
    },
    activated() {
        this.isActive = true
    },
    deactivated() {
        this.isActive = false        
    },
    mounted() {
        this.initImageData()
        // this.addSomeEvent()
        window.addEventListener('scroll', e => {
            this.handleScrol(window.pageYOffset)
        })
    },
    methods: {
        async initImageData () {
            // 轮播图
            this.sliderImg = await linkimg.getSliderImg()
            // section2
            this.section2Data = await linkimg.getSection2Data()
            // section3
            this.section3Data = await linkimg.getSection3Data()
            // section4
            this.section4Data = await linkimg.getSection4Data()
            // section5
            this.section5Data = await linkimg.getSection5Data()
            // section6
            this.section6Data = await linkimg.getSection6Data()
        },
        handleScrol(x) {
            if (x > 630 && x < 1670) {
                let offset = (x - 960) * 0.253
                // console.log('move - ' + 264 / 1040)
                this.$refs.mask.style.transform = `translate3d(0, ${offset}px, 0)`
            }
        }
    },
    components: {
        Slider, Section2, Section3, Section4, Section5, Section6, Section7, Footer
    }
}
</script>
<style lang="less" scoped>
.homepage{
    background: #fff;
    overflow: hidden;
    // min-width: 1200px;
    .section1{
        padding-top: 80px;
        height: 700px;
    }
    .section2{
        padding: 70px;
    }
    .section3{
        padding: 110px 0 250px 0;
        position: relative;
        background: transparent;
        overflow: hidden;
        &::before{
            content: '';
            position: absolute;
            width: 100%;height: 100%;
            left: 0; top: 0;
            background: rgba(0, 0, 0, 0.53);
            z-index: 3;
        }
        .mask{
            width: 100%;height: 924px;
            position: absolute;
            left: 0;bottom: 0;
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
        }
    }
    .section4{
        position: relative;
        z-index: 7;
    }
    .section5{
        // padding: 70px 0;
    }
    .section6{
        // padding-bottom: 20px;
    }
    .section7{

    }
    .footer-wrapper{
        background-color: #e0c073;
    }
    .main{
        min-width: 1200px;
    }
   
}
@media screen and(max-width: 1300px){
    .homepage {
        .section2{
            padding: 70px 0;
        }
    }
    .main {
        min-width: auto!important;
    }
}
@media screen and(max-width: 1100px){
    .homepage {
        
    }
    .main {
        min-width: auto!important;
    }
}
@media screen and(max-width: 400px){
    .homepage {
        .section1{
            padding-top: 0;
            height: 250px;
        }
    }
}
</style>



