<template>
    <div class="wrapper">
        <p class="title" @click="goFood">
            <span>特</span>
            <span>色</span>
            <span>美</span>
            <span>食</span>
        </p>
        <div class="delicious-wrapper" v-if="datas.length">
            <div class="container-left container">
                <div class="img" :style="{'background-image': `url('${datas[1].imgUrl}')`}"></div>
                <Card @on-click='handleClick' :data='datas[0]'/>
            </div>
            <div class="container-right container">
                <Card @on-click='handleClick' :data='datas[2]'/>
                <div class="img" :style="{'background-image': `url('${datas[3].imgUrl}')`}"></div>
            </div>
        </div>
    </div>    
</template>
<script>
import Card from './card'
export default {
    props: {
        datas: {
            type: Array,
            default: () => []
        }
    },
    methods: {
        goFood () {
            this.$router.push({
                name: `imagelist`, 
                query: {
                    cateid: 3,
                    cateindex: 0
            }})
        },
        handleClick () {
            this.$router.push({
                name: `imagelist`, 
                query: {
                    cateid: 3,
                    cateindex: 1
            }})
        }
    },
    components: {
        Card
    }
}
</script>
<style lang="less" scoped>
.wrapper{
    .title{
        height: 80px;
        line-height: 80px;
        font-size: 3em;
        padding: 40px 0;
        cursor: pointer;
        span{
            display: inline-block;
            padding: 0 10px;
            border: 1px solid #e0c073;
            color: #e0c073;
        }
    }
    .delicious-wrapper{
        .container{
            float: left;
            width: 50%;
            .img{
                height: 300px;
                background-size: cover;
                background-position: center;
            }
        }
        &::after{
            content: '';
            display: block;
            clear: both;
        }
    }
}
@media screen and(max-width: 950px) {
    .wrapper{
        .delicious-wrapper {

        }
    }
}
@media screen and(max-width: 420px) {
    .wrapper{
        .delicious-wrapper {
            .container{ 
                width: 100%;
            }
        }
    }
}
</style>



