<template>
    <div class="card">
        <div class="img" :style="{'background-image': `url('${data.imgUrl}')`}"></div>
        <div class="text">
            <p class="title">{{data.title}}</p>
            <p class="desc">{{data.desc}}</p>
        </div>
    </div>    
</template>
<script>
export default {
    props: {
        data: {
            type: Object,
            default:() => {
                return {
                    title: '标题',
                    desc: '描述',
                    imgUrl: require('@/common/img/cont2.png')
                }
            } 
        }
    }
}
</script>
<style lang="less" scoped>
.card{
    padding: 15px;
    box-sizing: border-box;
    text-align: center;
    .img{
        height: 130px;
        width: 130px;
        border-radius: 50%;
        background-size: 100% 100%;
        display: inline-block;
    }
    .text{
        .title{
            margin: 20px 0;
            color: #fff;
            font-size: 20px;
        }
        .desc{
            line-height: 1.7em;
            color: #999;
        }
    }
}
</style>



