export function iScrollTo (dest, distance) {
    let now = distance
    let every = (distance - dest) / 20
    return (function () {
        let timer = setInterval(() => {
            now -= every
            if (now > dest) {
                window.scrollBy(0, -every)
            } else {
                clearInterval(timer)
            }
        }, 20)
    })()
}


