export default {
    state: {
        height: 0,
        width: 0
    },
    getters: {
        height: state => state.height,
        width: state => state.width
    },
    mutations: {
        resetHeight (state, height) {
            state.height = height
        },
        resetWidht (state, width) {
            state.width = width
        }
    },
    actions: {
        resets ({commit}, {height, width}) {
            commit('resetHeight', height)
            commit('resetWidht', width)
        }
    }
}


