<template>
    <div class="footer">
        <div class="content">
            <p>&copy;重庆市巴南区秀泉映月酒店 版权所有</p>
            <p>酒店总机：023-66450999 </p>
            <p>销售部：023-66450250、66457789、66456088</p>
            <p>酒店地址：重庆市巴南区东泉镇东泉正街102号 邮政编码：401342</p>
        </div>
        <div class="icons">
            <div class="qrs">
                <div class="icon">
                    <!-- <div class="qr">
                        <img :src="require('common/img/qr_code01.jpg')">
                    </div> -->
                    <span class="iconfont icon-weibo"></span>
                </div>
                <div class="icon">
                    <div class="qr">
                        <img :src="qr1">
                    </div>
                    <span class="iconfont icon-weixin"></span>
                </div>
                <div class="icon">
                    <!-- <div class="qr">
                        <img :src="require('common/img/qr_code01.jpg')">
                    </div> -->
                    <span class="iconfont icon-web-icon-"></span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { getQr } from '@/api/linkimg'
export default {
    data () {
        return {
            icons: [],
            qr1: ''
        }
    },
    mounted() {
        this.qr1 = getQr(1)
    },
}
</script>
<style lang="less" scoped>
.footer{
    height: 100%;
    width: 1170px;
    margin: 0 auto;
    position: relative;
    font-size: 13px;
    .content{
        p{
            line-height: 20px;
            text-align: center;
            &:first-of-type{
                padding-top: 10px;
            }
        }
    }
    .icons{
        position: absolute;
        right: 0;top: 26px;
        .qrs{
            width: 100%;
            display: flex;
            justify-content: center;
            .icon{
                color: #e0c073;
                border-radius: 50%;
                cursor: pointer;
                padding: 0 15px;
                position: relative;
                .iconfont{
                    font-size: 30px;
                    background-color: white;
                    border-radius: 50%;
                    padding: 5px;
                }
                .qr{
                    position: absolute;
                    width: 100px;
                    height: 0;
                    top: -20px;
                    left: 0;
                    transform: translate(-18px, -100%);
                    opacity: 0;
                    img{
                        height: 100%;
                        width: 100%;
                    }
                    &::before{
                        content: '';
                        display: block;
                        position: absolute;
                        bottom: -10px;left: 45px;
                        border-top: 10px solid white;
                        border-left: 5px solid transparent;
                        border-right: 5px solid transparent;
                    }
                }
                &:hover{
                    .qr{
                        height: 100px;
                        opacity: 1;
                        transition: opacity .5s, height .5s;
                    }
                }
            }
        }
    }
}
@media screen and(max-width: 1200px) {
    .footer {
        width: 100%;
        .icons{
            top: 35px;
            .qrs{
                .icon{
                    .iconfont{
                        font-size: 20px;
                    }
                }
            }
        }
    }
}
@media screen and(max-width: 700px) {
    .footer {
        .icons{
            display: none;
        }
    }
}
</style>




